<template>
  <div class="container">
    <h3>Login</h3>
    <form>
      <div v-if="errorMessage" class="alert alert-danger">
        {{ errorMessage }}
      </div>
      <div v-if="showSuccessMessage" class="alert">Login Sucessful</div>
      <div class="form-group">
        Username:
        <input
          type="text"
          name="username"
          v-model="username"
          class="form-control"
        />
      </div>
      <div class="form-group">
        Password:
        <input
          type="password"
          name="password"
          v-model="password"
          class="form-control"
        />
      </div>
    </form>
    <button @click="login()" class="btn btn-success">Login</button>
  </div>
</template>
<script>
import UserDataService from "../service/UserDataService";

export default {
  name: "Login",
  components: {},
  data() {
    return {
      username: "",
      password: "",
      errorMessage: "",
      showSuccessMessage: false
    };
  },
  methods: {
    async login() {

      if(!this.username){
        this.errorMessage = "Enter your username"
        return;
      } else if(!this.password){
        this.errorMessage = "Enter your password"
        return;
      }

      const result = await UserDataService.loginUser({
        username: this.username,
        password: this.password,
      });
      
      if (result.message) {
        this.errorMessage = result.message
        return
      }
      
      this.showSuccessMessage = true
      this.$emit('login', result);
      console.log('result: ',result);
      UserDataService.storeUserData(result)
      
      this.$router.push({ name: "Home" });
    },
  },
};
</script>